import React from 'react';
import { Form, Icon, Input, Button, Checkbox, Layout, Card } from 'antd';

const Loading = (props) => {
    return (
        <Layout
            style={{
                height: '100vh',
                justifyContent: 'center',
                alignItems: 'center',
                background: "#FFF"
            }}
        >
           <Icon type="loading" style={{fontSize :80}} />
        </Layout >
    )
}
export default Loading